    margin: 0;
    padding: 0;
    list-style: none;
    border: none;
}

body, button {
    font-family: Hanken Grotesk, SF Pro, Roboto, 'sans-serif';
    font-size: 1rem;
    line-height: 1.5;
    letter-spacing: .02rem;
}


.header {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    background-color: #ffffff40;
    backdrop-filter: blur(1rem);
    align-items: center;
    padding-left: 2rem;
    position: sticky;
    top: 0;
    z-index: 1;
}

.logo {
    justify-self: start;
    text-transform: uppercase;
    color:#000000;
    font-weight: 400;
    font-size: 1.5rem;
}

.logo:hover {
    font-weight: 400;
    cursor: pointer;
    color: #a30000;
    transition: ease 0.3s;
}

.navlist {
    display: flex;
    flex-direction: row;
    gap: 2rem;
    justify-self: center;
}

button {
    border: 2px solid #000000;
    box-sizing: border-box;
    background-color: #ffffff;
    color:#000000;
    transition: ease-in-out 0.3s;
    padding: 1rem;
}

button:hover {
    border: 2px solid #a30000;
    background-color: #a30000;
    color: #ffffff;
    transition: ease-in-out 0.2s;
    cursor: pointer;
}

.header > button.reserve {
    padding: 1.5rem 2rem;
    border: none;
    background-color: #430000;
    color: #ffffff;
    justify-self: end;
}

.header > button.reserve:hover {
    padding: 1.5rem 2rem;
    background-color: #a30000;
    color: #ffffff;
    justify-self: end;
}

.listItem > button {
    border: 2px solid #ffffff00;
    padding: 0.5rem 1rem;
    background-color: #ffffff00;
}

.listItem > button:hover, button.active {
    color:#000000;
    background-color: #ff9494;
}

.hero, .menu {
    color: #000000;
    background-color: #fcc6c6;
    padding: 6rem 2rem;
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
    align-items: center;
}

h1 {
    font-size: 5rem;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 0.5rem;
    background-color: #ff9494b0;
    padding: 0rem 2rem;
}

h2 {
    text-transform: uppercase;
    font-size: 2rem;
    font-weight: 300;
}

h3 {
    text-transform: uppercase;
    font-size: 2rem;
    font-weight: 300;
}

h4 {
    text-transform: uppercase;
    font-size: 1.25rem;
    font-weight: 300;
}

.actions {
    display: flex;
    flex-direction: row;
    padding-top: 2rem;
    gap: 2rem;
}

.actions > button {
    width: 200px;
}

.specials {
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 5rem 2rem;
}

.flexbox {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 2rem; 
    justify-content: center;
}

.card {
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    flex: 1;
    background-color: #fcc6c6;
    backdrop-filter: blur(0.5rem);
    font-weight: 300;
    min-width: 240px;
    max-width: 400px;
}

.card > h4 {
    margin-left: 0.5rem;
    color: #000000;
}

.image {
    height: 300px;
    background-color: #ffffff;
}

a, a:visited {
    color:#000000;
}

a:hover {
    color: #430000;
}

.footer {
    background-color: #000000;
    padding: 0rem 2rem 6rem;
    color:#fcc6c6;
    display: flex;
    flex-direction: column;
}

a.socialLink, a.listLink {
    color: #ff9494;
    text-decoration: none;
}

a.socialLink:hover, a.listLink:hover {
    color: #ffffff;
    text-decoration: underline;
}

.socialsList {
    display: flex;
    flex-direction: row;
    gap: 2rem;
    padding-bottom: 2rem;
    align-self: end;
    padding: 2rem 0rem;
}

.linkList {
    display: flex;
    flex-direction: row;
    gap: 2rem;
    padding: 1rem 0rem;
    border-top: 1px solid #bd5555;
}

.copyright {
    color: #bd5555;
}

.address {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
    padding: 5rem;
    align-items: center;
    background-color:#f5f5f5;
}

.address > h3 {
    font-size: 3rem;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 0.5rem;
    background-color: #ff9494b0;
    padding: 0rem 2rem;
    margin-bottom: 2rem;
}

.location {
    text-align: center;
}

.menu {
    gap: 2rem;
    background-color: #f5f5f5;
}

.menu > h1 {
    background-color: #ffffff;
    color: #a30000;
}

.starters, .mains, .desserts {
    width: 80%;
    padding: 3rem 0rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    border-bottom: 1px solid #a30000;
    text-transform: uppercase;
}

.desserts {
    border-bottom: none;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,gBAAgB;IAChB,YAAY;AAChB;;AAEA;IACI,yDAAyD;IACzD,eAAe;IACf,gBAAgB;IAChB,sBAAsB;AAC1B;;;AAGA;IACI,aAAa;IACb,kCAAkC;IAClC,uBAAuB;IACvB,2BAA2B;IAC3B,2BAA2B;IAC3B,mBAAmB;IACnB,kBAAkB;IAClB,gBAAgB;IAChB,MAAM;IACN,UAAU;AACd;;AAEA;IACI,mBAAmB;IACnB,yBAAyB;IACzB,aAAa;IACb,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;IAChB,eAAe;IACf,cAAc;IACd,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,oBAAoB;AACxB;;AAEA;IACI,yBAAyB;IACzB,sBAAsB;IACtB,yBAAyB;IACzB,aAAa;IACb,4BAA4B;IAC5B,aAAa;AACjB;;AAEA;IACI,yBAAyB;IACzB,yBAAyB;IACzB,cAAc;IACd,4BAA4B;IAC5B,eAAe;AACnB;;AAEA;IACI,oBAAoB;IACpB,YAAY;IACZ,yBAAyB;IACzB,cAAc;IACd,iBAAiB;AACrB;;AAEA;IACI,oBAAoB;IACpB,yBAAyB;IACzB,cAAc;IACd,iBAAiB;AACrB;;AAEA;IACI,2BAA2B;IAC3B,oBAAoB;IACpB,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,yBAAyB;AAC7B;;AAEA;IACI,cAAc;IACd,yBAAyB;IACzB,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,YAAY;IACZ,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,yBAAyB;IACzB,sBAAsB;IACtB,2BAA2B;IAC3B,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;IACzB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,iBAAiB;IACjB,SAAS;AACb;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,eAAe;IACf,SAAS;IACT,uBAAuB;AAC3B;;AAEA;IACI,eAAe;IACf,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,OAAO;IACP,yBAAyB;IACzB,6BAA6B;IAC7B,gBAAgB;IAChB,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,yBAAyB;AAC7B;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,yBAAyB;IACzB,uBAAuB;IACvB,aAAa;IACb,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,cAAc;IACd,qBAAqB;AACzB;;AAEA;IACI,cAAc;IACd,0BAA0B;AAC9B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,oBAAoB;IACpB,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,kBAAkB;IAClB,6BAA6B;AACjC;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,sB